import { ActivatedRoute } from '@angular/router';
import { Validators } from "@angular/forms";
import { FormValidationType } from "./../../models/from-validation-message";
import { FormHelperService } from "./../../services/form-helper.service";
import { EnvService } from "src/app/services/env.service";
import { AlertService } from "src/app/services/alert.service";
import { NavController, MenuController } from "@ionic/angular";
import { AuthService } from "./../../services/auth.service";
import { FormValidationMessageField } from "src/app/models/from-validation-message";
import { FormControl } from "@angular/forms";
import { FormGroup } from "@angular/forms";
import { Component, OnInit } from "@angular/core";
import { catchError } from "rxjs/operators";
import { of } from "rxjs";

@Component({
  selector: "app-forgot-password-password",
  templateUrl: "./forgot-password-password.page.html",
  styleUrls: ["./forgot-password-password.page.scss"],
})
export class ForgotPasswordPasswordPage implements OnInit {
  requesting: boolean = false;
  forgotPasswordForm = new FormGroup({
    phone: new FormControl({ value: "", disabled: true }),
    verificationCode: new FormControl("", Validators.required),
    password: new FormControl("", Validators.required),
  });
  validationMessages: FormValidationMessageField[] = [];

  submitDisabled = false;
  constructor(
    private authService: AuthService,
    private navCtrl: NavController,
    private alertService: AlertService,
    private menu: MenuController,
    private env: EnvService,
    private formHelper: FormHelperService,
    private route:ActivatedRoute
  ) {
    this.formHelper.addValidationMessage(
      this.validationMessages,
      "verificationCode",
      FormValidationType.REQUIRED,
      "کد اعتبارسنجی الزامی است."
    );
    this.formHelper.addValidationMessage(
      this.validationMessages,
      "password",
      FormValidationType.REQUIRED,
      "گذرواژه الزامی است."
    );
  }
  ngOnInit(): void {
    this.forgotPasswordForm.controls.phone.setValue( this.route.snapshot.params.phoneNumber);

  }
  changePass(form: FormGroup) {
    if (!this.formHelper.validateForm(this.validationMessages, form)) {
      return;
    }
    this.requesting = true;
    this.authService
      .retrievePassword(
        form.controls.phone.value,
        form.controls.password.value,
        form.controls.verificationCode.value
      )
      .pipe(
        catchError((error) => {
          const errorType = error["error"]["error"];
          this.alertService.presentError(
            this.env.serverErrorMessage(error.error["error_code"])
          );

          return of(false);
        })
      )
      .subscribe((result) => {
        this.requesting = false;
        if (result) {
          this.alertService.presentSuccess("اکنون با گذرواژّ جدید وارد شوید.");
          this.navCtrl.navigateRoot(["/login"]);
        }
      });
  }
  ionViewWillEnter() {
    this.menu.enable(false);
  }
  ionViewWillLeave() {
    this.menu.enable(true);
  }
}
